html, body {
	margin: 0; }
.App {
	padding: 5px;
	display: flex;
	justify-content: center;
	flex-direction: column;
	min-height: 100vh;
	.card {
		min-height: 400px;
		height: 400px;
		padding: 5px;
		display: flex;
		justify-content: center;
		align-items: center; }
	.btn {
		flex-grow: 1;
		display: flex;
		justify-content: center;
		align-items: center; } }
